.App {
  text-align: center;
  font-family: Arial, sans-serif;
}

header {
  background: #111;
  color: white;
  padding: 20px 0;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  margin: 20px;
}

.gallery img {
  width: 300px;
  height: auto;
  object-fit: cover;
  border: 2px solid #ccc;
  transition: transform 0.3s ease;
}

.gallery img:hover {
  transform: scale(1.05);
  border-color: #666;
}

footer {
  margin-top: 40px;
  padding: 10px;
  background-color: #f1f1f1;
}
